	module.exports = {"data":{"post":{"html":"<p>We missed (er, deliberately skipped) the first-ever German TV debate between chancellor candidates Sunday evening. It sounds like we didn't miss much. The consensus from the media, my co-workers, and the German weblogs I've scanned was the that the format was forced and sterile, Schröder was more convincing,  Stoiber came across better than expected, and the whole thing was rather boring. And they will do it all over again in a couple of weeks. Here are a few English headlines snitched from <a href=\"http://www.newsisfree.com/\">News Is Free</a>.</p>\n<p>International Herald Tribune: Europe <a href=\"http://www.iht.com/articles/68843.htm\">NEWS ANALYSIS Stoiber wins some respect in first TV debate with Schroeder</a> \"<em>Edmund Stoiber may not have won Germany's first televised debate between chancellor candidates, but he performed so much better than the meager expectations of the German political world that he emerged into the election's homestretch with both momentum and a new measure of respect.</em>\"</p>\n<p>Bloomberg World News <a href=\"http://quote.bloomberg.com/fgcgi.cgi?ptitle=Top%20World%20News&s1=blk&tp=ad_topright_topworld&T=markets_box.ht&s2=ad_right1_windex&bt=ad_position1_windex&box=ad_box_all&tag=worldnews&middle=ad_frame2_windex&s=APWnnlha0R2VybWFu\">Germany's Opposition Leader Stoiber Wins Support After Debating Schroeder</a> \"<em>German chancellor candidate Edmund Stoiber won more support from voters than Chancellor Gerhard Schroeder in the country's first televised campaign debate a month before elections, opinion polls showed.</em>\"</p>\n<p>AFXPress: Europe <a href=\"http://www.afxpress.com/afxpress2/afx/story_43817.xml.html\">GERMAN VOTE Schroeder emerges as more competent than Stoiber in 1st TV debate</a> \"<em>BERLIN (AFX) - German chancellor Gerhard Schroeder came away as more competent than his main rival Edmund Stoiber in Germany's first-ever live television debate last night, nearly four weeks ahead of a tightly contested election, snap opinion surveys said.</em>\"</p>\n<p>Radio Free Europe <a href=\"http://www.rferl.org/nca/features/2002/08/26082002135714.asp\">Germany: Schroeder, Stoiber Discuss Iraq, Economy In First-Ever Election Debate</a> \"<em>German Chancellor Gerhard Schroeder and his challenger in next month's election, Edmund Stoiber, clashed in a U.S.-style television debate last night, the first-ever held in a German election. The two candidates sparred over German support for any U.S. attack against Iraq, as well as their different approaches to the problems of German unemployment and the sagging economy. Opinion polls were inconclusive on who won the debate, but commentators minimized its impact on changing voters' minds.</em>\"</p>","fields":{"title":"The Not-So-Great Debate","date":"27 Aug 2002","url":"https://papascott.de/2002-08-27-the-not-so-great-debate/"}}},"pathContext":{"slug":"/2002-08-27-the-not-so-great-debate/","prev":{"fields":{"title":"An Evil Idea","date":"25 Aug 2002","slug":"/2002-08-25-an-evil-idea/"}},"next":{"fields":{"title":"'Perl and XML' For Sale","date":"29 Aug 2002","slug":"/2002-08-29-perl-and-xml-for-sale/"}}}}

